import React, { useState } from 'react';
import data from './data';
function App() {
  const [count, setCount] = useState(0);
  const [text, setText] = useState([]);
  const handleSubmit = (e) => {
    e.preventDefault();
    const shuffledArray = data.sort((a, b) => 0.5 - Math.random());
    console.log(shuffledArray.length);
    let amount = parseInt(count);
    if (count > shuffledArray.length) {
      for (let i = 0; i < count - (shuffledArray.length - 1); i++) {
        const randomElement =
          shuffledArray[Math.floor(Math.random() * shuffledArray.length)];
        shuffledArray.push(randomElement);
      }
    }
    setText(shuffledArray.slice(0, amount));
  };

  return (
    <section className="section-center">
      <h3>Tired of boring lorem ipsum?</h3>
      <h4>Try Manuel's Ipsum</h4>
      <form className="lorem-form" onSubmit={handleSubmit}>
        <label htmlFor="amount">paragraps:</label>
        <input
          type="number"
          id="amount"
          name="amount"
          value={count}
          min="0"
          onChange={(e) => setCount(e.target.value)}
        />
        <button type="submit" className="btn">
          Generate
        </button>
      </form>
      <article className="lorem-text">
        {text.map((item, index) => {
          return <p key={index}>{item}</p>;
        })}
      </article>
    </section>
  );
}

export default App;
